@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
* {
  box-sizing: border-box;
  font-size: 10px;
}

html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-family: "DM Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #313131;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main {
  margin-top: 0;
  width: 90%;
  max-width: 960px;
  margin: 0 auto;
  display: flex;
  height: 100%;
}

.Responsive-footer {
  display: none;
}

@media all and (max-width: 700px) {
  .Responsive-footer {
    order: 3;
    display: block;
    width: 100%;
  }

  main {
    flex-wrap: wrap;
  }

  .Responsive-nav {
    display: none !important;
  }
}

.Greeting-wrapper {
  flex-grow: 2;
  order: 2;
  padding-top: 180px;
  padding-bottom: 50px;
  z-index: 1;
  width: 50%;
  min-width: 50%;
  height: 100vh;
  min-height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.ProjectSection {
  width: 50%;
  flex-grow: 2;
  order: 2;
  padding-top: 180px;
  z-index: 1;
  width: 50%;
  min-width: 50%;
}

.Mugshot {
  border-radius: 100%;
  width: 150px;
}

.Hello {
  margin: 15px 0;
  font-size: 5rem;
  line-height: 5.5rem;
  font-weight: 600;
}

.Description {
  margin: 15px 0;
  font-size: 2rem;
  font-weight: 400;
}

.Spacer {
  flex: 1 1;
}

@media all and (max-width: 700px) {
  .Greeting-wrapper {
    order: 1;
    padding-top: 120px;
    padding-bottom: 0;
    position: unset;
    height: auto;
  }
  article .Footer-wrapper {
    display: none;
  }

  .Greeting-wrapper {
    position: relative;
    width: 100%;
    top: 0;
  }

  .Mugshot {
    width: 100px;
  }

  .Hello {
    font-size: 5rem;
    line-height: 5.5rem;
    margin-top: 15px;
    margin-bottom: 0;
  }

  .Description {
    font-size: 2em;
    margin-top: 15px;
    margin-bottom: 0;
  }
}

.Footer-wrapper {
  width: 90%;
  max-width: 960px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  display: flex;
}

.Socials {
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Copyright {
  text-align: right;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 0;
}

svg {
  fill: rgba(255, 255, 255, 0.6);
  transition: fill 1s ease;
}

svg:hover {
  fill: white;
}

@media all and (max-width: 700px) {
  .Footer-wrapper {
    align-items: center;
    margin: 50px auto 50px auto;
    position: relative;
  }
}

.ProjectSection {
  width: 50%;
  flex-grow: 2;
  order: 2;
  padding-top: 180px;
  z-index: 1;
  width: 50%;
  min-width: 50%;
}

.Arrow {
  cursor: pointer;
  display: none;
  border: none;
}

.iframe-wrapper {
  position: relative;
  overflow: hidden;
  padding-top: 56.25%;
}

.resp-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.Project-image {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

.Project-category {
  font-size: 1rem;
  margin-bottom: 0;
}

.Project-title {
  font-size: 3rem;
  margin-top: 0;
}

.Project-description {
  font-size: 1.5rem;
  color: inherit;
}

@media all and (max-width: 700px) {
  .Arrow {
    display: block;
    margin-bottom: 1.5em;
  }

  .ProjectSection {
    order: 1;
    padding-top: 120px;
    position: unset;
    height: auto;
  }
  article .Footer {
    display: none;
  }
  .Project-image {
    left: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
    max-width: 100vw;
    position: relative;
    right: 50%;
    width: 100vw;
    border-radius: 0;
  }
}

.Title-view-more {
  grid-column: 1/3;
  display: block;
  font-size: 2.5rem;
}

.View-more-wrapper {
  order: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
  z-index: 0;
  width: 50%;
  min-width: 200px;
  padding-bottom: 20px;
}

.Project {
  border-radius: 10px;
  padding: 10px;
  color: black;
  box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: flex-end;
  min-width: 120px;
}

.Project::before {
  content: "";
  padding-bottom: 100%;
  display: inline-block;
  vertical-align: top;
}

.Project:hover {
  box-shadow: 8px 8px 20px 0px rgba(0, 0, 0, 0.4);
  transform: translate3d(0, -2px, 0);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.Category {
  margin: 0;
  font-weight: 400;
  font-size: 1rem;
  color: black;
}

.Title-project {
  margin: 0;
  font-weight: 800;
  font-size: 1.5rem;
  color: black;
}

.Prev {
  text-align: center;
  font-size: 1.2em;
  margin-top: 30px;
  color: white;
}

.Next {
  text-align: center;
  font-size: 1.2em;
  margin-top: 30px;
  color: white;
}

@media all and (max-width: 700px) {
  .View-more-wrapper {
    padding-top: 0;
    width: 100%;
    margin-right: auto;
    order: 2;
    padding-bottom: 0;
  }
}

.App-header {
  background-color: rgba(49, 49, 49, 0.8);
  width: 100%;
  height: 170px;
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  display: flex;
  position: fixed;
  top: 0;
  align-items: center;
  z-index: 2;
}

.Header-contents {
  display: flex;
  width: 90%;
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}

.Logo {
  width: 150px;
  fill: white;
}

@media all and (max-width: 700px) {
  .App-header {
    height: 120px;
    z-index: 2;
    order: 2;
  }
  .Header-contents {
    justify-content: center;
  }
  .Logo {
    width: 130px;
  }
}

.Title-nav {
  display: none;
}

.Nav-wrapper {
  padding-top: 180px;
  padding-bottom: 50px;
  order: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
  z-index: 0;
  margin-right: 6rem;
}

.Project {
  border-radius: 10px;
  padding: 10px;
  color: black;
  box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: flex-end;
}

