@media all and (max-width: 700px) {
  .Nav-wrapper {
    padding-top: 0;
    width: 100%;
    margin-right: auto;
    order: 2;
    padding-bottom: 0;
  }

  .Title-nav {
    grid-column: 1/3;
    display: block;
    font-size: 2.5rem;
  }
}

